<template lang="html">
  <div class="">
    <h3>修改密码<span>　/　说明</span></h3>
    <div class="pass-wrapper">
      <div class="pass-wrapper-header">
        <h2>
          <span>修改登录密码</span>　｜　
          <span class="act">修改支付密码</span>
        </h2>
        <div class="pass-wrapper-form">
          <el-form label-width="80px">
            <el-form-item label="企业代码">
              <el-input placeholder="请输入企业代码"></el-input>
            </el-form-item>
            <el-form-item label="原密码">
              <el-input type="password" placeholder="请输入旧密码"></el-input>
            </el-form-item>
            <el-form-item label="新密码">
              <el-input type="password" placeholder="请输入新密码"></el-input>
            </el-form-item>
            <el-form-item label="确认密码">
              <el-input type="password" placeholder="请重新输入旧密码"></el-input>
            </el-form-item>
            <el-form-item label="手机号码">
              <el-input placeholder="请填写手机号码"></el-input>
            </el-form-item>
            <el-form-item label="验证码">
              <el-input placeholder="请输入验证码"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="info">提交</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
}
</script>

<style lang="less">
h3{
  font-size: 18px;
  color: #2e51a5;
  line-height: 80px;
  span{
    color: #9199AE;
    font-size: 13px;
    font-weight: 500;
  }
}
.pass-wrapper{
  background: white;
  width: 92%;
  border: 1px solid #e6e9f2;
  padding: 0 38px 0px 38px;
  h2{
    line-height: 80px;
    font-size: 20px;
    color: #999;
    border-bottom: 1px solid #e6e9f2;
    span.act{
      color: #2e51a5;
      font-weight: bold;
    }
  }
  .pass-wrapper-form{
    width: 25%;
    margin: 0 auto;
    padding-top: 35px;
    .el-form-item{
      .el-button{
        width: 100%;
      }
      .el-form-item__label{
        color: #8B98B5;
        font-weight: bolder;
      }
    }
  }
}
</style>



